// /components/users/UserCard.js
import { useState } from 'react';
import UserForm from './UserForm';
import { userService } from '../../../lib/services/UserService';
import styles from '../../../styles/components/UserCard.module.css';

export default function UserCard({ user, isSaved = false, onSave, onUpdate }) {
  const [editing, setEditing] = useState(false);
  const [editForm, setEditForm] = useState({});

  const handleEdit = () => {
    setEditing(true);
    setEditForm(user);
  };

  const handleSaveEdit = async () => {
    try {
      await userService.updateUser(user.id, editForm);
      setEditing(false);
      setEditForm({});
      onUpdate?.();
    } catch (error) {
      console.error('Error updating user:', error);
      alert('Error updating user');
    }
  };

  const handleDelete = async () => {
    if (confirm('Are you sure you want to delete this user?')) {
      try {
        await userService.deleteUser(user.id);
        onUpdate?.();
      } catch (error) {
        console.error('Error deleting user:', error);
        alert('Error deleting user');
      }
    }
  };

  const handleCancelEdit = () => {
    setEditing(false);
    setEditForm({});
  };

  const handleInputChange = (field, value) => {
    setEditForm(prev => ({
      ...prev,
      [field]: value
    }));
  };

  if (editing) {
    return (
      <UserForm
        user={editForm}
        onSave={handleSaveEdit}
        onCancel={handleCancelEdit}
        onChange={handleInputChange}
      />
    );
  }

  return (
    <div className={styles.userCard}>
      <div className={styles.userInfo}>
        <h3>{user.first_name} {user.last_name}</h3>
        <p><strong>Username:</strong> {user.username}</p>
        <p><strong>Email:</strong> {user.email}</p>
        <p><strong>Phone:</strong> {user.phone_number}</p>
        <p><strong>Date of Birth:</strong> {user.date_of_birth}</p>
        <p><strong>UID:</strong> {user.uid}</p>
        {user.gender && <p><strong>Gender:</strong> {user.gender}</p>}
        {user.nationality && <p><strong>Nationality:</strong> {user.nationality}</p>}
      </div>
      
      <div className={styles.userActions}>
        {!isSaved && onSave && (
          <button 
            onClick={() => onSave(user)}
            className={styles.saveBtn}
          >
            Save to CSV
          </button>
        )}
        {isSaved && (
          <>
            <button 
              onClick={handleEdit}
              className={styles.editBtn}
            >
              Edit
            </button>
            <button 
              onClick={handleDelete}
              className={styles.deleteBtn}
            >
              Delete
            </button>
          </>
        )}
      </div>
    </div>
  );
}