// /components/users/UserList.js
import UserCard from './UserCard';
import styles from '../../../styles/components/UserList.module.css';

export default function UserList({ users, isSaved = false, onSave, onUpdate }) {
  if (users.length === 0) {
    return <p>No users found.</p>;
  }

  return (
    <div className={styles.userList}>
      {users.map((user) => (
        <UserCard
          key={user.uid || user.id}
          user={user}
          isSaved={isSaved}
          onSave={onSave}
          onUpdate={onUpdate}
        />
      ))}
    </div>
  );
}