// /lib/hooks/useUsers.js
import { useState } from 'react';
import { userService } from '../services/UserService';

export function useUsers() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const fetchUsers = async () => {
    setLoading(true);
    setError('');
    try {
      const result = await userService.fetchUsers(10);
      if (result.success) {
        setUsers(result.data);
      } else {
        setError(result.message || 'Failed to fetch users');
      }
    } catch (err) {
      console.error('Error fetching users:', err);
      setError('Network error: Unable to fetch users');
    } finally {
      setLoading(false);
    }
  };

  const saveUser = async (user) => {
    try {
      await userService.saveUser(user);
    } catch (err) {
      console.error('Error saving user:', err);
      throw err;
    }
  };

  const saveAllUsers = async () => {
    setLoading(true);
    try {
      let successCount = 0;
      let errorCount = 0;
      
      for (const user of users) {
        try {
          await saveUser(user);
          successCount++;
        } catch (error) {
          errorCount++;
        }
      }
      
      alert(`Saved ${successCount} users successfully. ${errorCount > 0 ? `${errorCount} failed.` : ''}`);
    } catch (error) {
      console.error('Error in saveAllUsers:', error);
      alert('Error saving users');
    } finally {
      setLoading(false);
    }
  };

  return {
    users,
    loading,
    error,
    fetchUsers,
    saveUser,
    saveAllUsers
  };
}