// /lib/hooks/useSavedUsers.js
import { useState, useEffect } from 'react';
import { userService } from '../services/UserService';

export function useSavedUsers() {
  const [savedUsers, setSavedUsers] = useState([]);
  const [error, setError] = useState('');

  const fetchSavedUsers = async () => {
    try {
      const users = await userService.getSavedUsers();
      setSavedUsers(users);
    } catch (err) {
      console.error('Error fetching saved users:', err);
      setError('Failed to load saved records');
    }
  };

  const updateSavedUsers = async () => {
    await fetchSavedUsers();
  };

  useEffect(() => {
    fetchSavedUsers();
  }, []);

  return {
    savedUsers,
    error,
    fetchSavedUsers,
    updateSavedUsers
  };
}