// /components/ui/Button.js
import styles from '../../../styles/components/ui/Button.module.css';

export function Button({ children, onClick, disabled, variant = 'primary', ...props }) {
  const className = `${styles.button} ${styles[variant]} ${disabled ? styles.disabled : ''}`;

  return (
    <button 
      onClick={onClick} 
      disabled={disabled}
      className={className}
      {...props}
    >
      {children}
    </button>
  );
}